import React from "react";
import styled from "@emotion/styled";
// import Header from "./Header";
import { ThemeToggler } from 'gatsby-plugin-dark-mode'
import Footer from "./Footer";
import { Grid, Typography } from "@material-ui/core";
import withStyles from "@material-ui/styles/withStyles";
import "../css/style.styl";
import MuiDrawer from "./muiDrawer";
import Cookies from 'universal-cookie';
const themeLoader = () => {
    const cookies = new Cookies();
    let theme = cookies.get('theme');
    console.log("Loaded theme: " + theme);
    // props.toggleTheme(theme);
    return theme;
};
const styles = theme => ({
  container: {
    padding: 15, //TU BYL MARGIN
    [theme.breakpoints.up('sm')]: {
      // backgroundColor: 'red',
      marginTop: 94,
    },
  },
  contentBox: {
    maxWidth: "calc(1136px - 60px)",
    width: "calc(100% - 60px)",
  },
  title: {
    textAlign: "center"
  }
});
//TODO:: pytanie czy wrapper jest potrzebny
const Wrapper = styled('div')`
  display: flex;
  justify-content: space-between;

  @media only screen and (max-width: 767px) {
    display: block;
  }
`;
//TODO:: try loading and setting theme on first mount of this component
class Page extends React.Component {
  render() {
    const { classes, title, children } = this.props;
    let loadedTheme = themeLoader();
    return (
      <>
          <div
              style={{
                  backgroundColor: 'var(--bg)',
                  color: 'var(--textNormal)',
                  transition: 'color 0.2s ease-out, background 0.2s ease-out',
              }}
          >
          <ThemeToggler theme={loadedTheme}>
              {({theme, toggleTheme}) => (
                  <Wrapper>
                      <MuiDrawer {...{theme, toggleTheme, loadedTheme}} />
                      <Grid
                          className={classes.container}
                          container
                          direction="row"
                          justify="center"
                      >
                          <Grid
                              className={classes.contentBox}
                              item
                          >
                              {title ? (
                                  <Typography
                                      className={classes.title}
                                      variant="h2"
                                      gutterBottom
                                  >
                                      {title}
                                  </Typography>
                              ) : null}
                              {children}
                              <Footer />
                          </Grid>
                      </Grid>
                  </Wrapper>
                  )}
          </ThemeToggler>
          </div>
      </>
    );
  }
}

export default withStyles(styles)(Page);
