import React from "react";
import { StaticQuery, graphql } from "gatsby";
import { Link } from "gatsby";
import Menu from "./Menu";
import MenuMobile from "./MenuMobile";
import Hidden from "@material-ui/core/Hidden";
import Grid from "@material-ui/core/Grid";
import Toolbar from "@material-ui/core/Toolbar";
import AppBar from "@material-ui/core/AppBar";
import Typography from "@material-ui/core/Typography";
import Chip from "@material-ui/core/Chip";
import Avatar from "@material-ui/core/Avatar";
import { MaterialUi } from "mdi-material-ui";
import Cookies from 'universal-cookie';

function Header(props) {
  //TODO:: dodac tutaj laczenie styli komponentu z przekazanymi przez properties
  //przekazane przez props nadpisuja komponentowe
  const { classes } = props;
  // const fromCookie = () => {
  //   if (props.loadedTheme !== props.theme) {
  //     props.toggleTheme(props.theme);
  //   }
  // };
  // fromCookie();
  const themeChange = e => {
    let theme = e.target.checked ? 'dark' : 'light';
    const cookies = new Cookies();
    cookies.set('theme', theme);
    console.log("Saved theme type:" + cookies.get('theme'));
    props.toggleTheme(theme);
  };
  return (
    <AppBar id="appBar" position="fixed" className={classes.appBar}>
      <Toolbar>
        <Grid
          container
          justify="space-between"
          alignItems="center"
          spacing={16}
        >
          <Grid item>
            {props.children}
            <Chip
              id="logo"
              variant="outlined"
              avatar={<Avatar id="logoIcon">
                <MaterialUi />
              </Avatar>}
              label={
                <Link to="/">
                  {props.data.site.siteMetadata.title.toUpperCase()}
                </Link>
              }
            />
            <label>
              <input
                  type="checkbox"
                  onChange={themeChange}
                  checked={props.theme === 'dark'}
              />{' '}
              Dark mode
            </label>
          </Grid>
          <Grid item>
            <Hidden mdUp>
              <MenuMobile />
            </Hidden>
            <Hidden smDown>
              <Typography
                  component="span"
                  variant="caption"
              >
                <Menu />
              </Typography>
            </Hidden>
          </Grid>
        </Grid>
        <Grid item />
      </Toolbar>
    </AppBar>
  );
};

export default props => (
  <StaticQuery
    query={graphql`
      query {
        site {
          siteMetadata {
            title
            contact {
              email
              phone
            }
          }
        }
      }
    `}
    render={data => <Header data={data} { ...props}/>}
  />
);
// export default Header;
